export default {
  state: {
    type: 'check',
    categories: [], //
    submitScoreData: { //
      "levelId": "", //idid
      "shopId": "", //id
      "categoryList": [
        // {
        //   "categoryId": "", //id
        //   "standardList": [{
        //     "standardId": "", //id
        //     "deduct": 0, //
        //     "reason": "", //
        //     "urls": [] //url
        //   }]
        // }
      ]
    },
    subcategories: [], //
    categoryListIndex: 0, //categoryList
    standardListIndex: 0, //standardList
    totalPoints: 0, //
    deductMarks: 0 //
  },
  mutations: {
    setType: (state, str) => state.type = str,
    setShopId: (state, shopId) => state.submitScoreData.shopId = shopId, //
    setLevelId: (state, levelId) => state.submitScoreData.levelId = levelId, //
    setCategories: (state, categories) => state.categories = categories, //
    setSubmitScoreData: (state, submitScoreData) => state.submitScoreData = submitScoreData, //
    setCategoryListIndex: (state, categoryListIndex) => state.categoryListIndex = categoryListIndex, //
    setStandardListIndex: (state, standardListIndex) => state.standardListIndex = standardListIndex, //
    setSubcategories: (state, subcategories) => state.subcategories = subcategories,
    setTotalPoints: (state, totalPoints) => state.totalPoints = totalPoints,
    setdeductMarks: (state, deductMarks) => state.deductMarks = deductMarks
  },
  getters: {
    getCategories: (state) => state.categories, //
    getSubmitScoreData: (state) => state.submitScoreData
  }
}


