import Common from './common'


class Mango extends Common {
  constructor() {
    super()
    this.key = true
    this.customerAjaxParams = (tenantId) => {
      return {
        page: 1,   //
        limit: 30,    //
        u: '',   //1:0
        i: '',   //1
        uo: 1,   // 1:0
        io: 1,   // //1
        key: '',     //
        startTime: '',
        endTime: '',
        tut: 0,   // ,
        tenantId: tenantId
      }
    }
    this.chartsBotTit = (res) => {
      return `${res.yAxisData[0]}           ${res.yAxisData[1]}        ${res.yAxisData[2]}`
    }
  }
}

export default new Mango()


