export default {
  inserted(el, binding, vnode) {
    const {
      value
    } = binding
    // [{'Store Manager':''},{'Sleep Consultant':''},{'Dealer Boss':''},{'supervisor':''}]
    //['Store Manager','Sleep Consultant','Dealer Boss']

    const roles = localStorage.getItem('certPositionType')

    if (value && value instanceof Array && value.length > 0) {
      const permissionRoles = value


      const hasPermission = permissionRoles.includes(roles)


      if (!hasPermission) {
        el.parentNode && el.parentNode.removeChild(el)
      }
    } else {
      throw new Error(`permission v-permission="['admin']"`)
    }
  }
}


